import { Component, OnInit } from '@angular/core';
import { AppService } from '../app.service';

@Component({
  selector: 'app-ubah-profil',
  templateUrl: './ubah-profil.component.html',
  styleUrls: ['./ubah-profil.component.css']
})
export class UbahProfilComponent implements OnInit {

  editUser = {
    name: '',
    birthday: '',
    gender: '',
    email: '',
    telephone: '',
    identity_number: '',
    photo : ''
  }

  constructor( private appService: AppService ) { }

  ngOnInit() {
  }


  submitEdit(){
    this.appService.editProfileUser(this.editUser).subscribe(editUser =>{
      console.log(editUser);
    })
  }
}



